'use client'

import { useState, useEffect } from 'react'
import Link from 'next/link'
import { 
  Zap, 
  Plus,
  AlertCircle,
  ExternalLink,
  TrendingUp,
  DollarSign,
  Activity,
  ArrowRight
} from 'lucide-react'

interface Agent {
  id: string
  name: string
  x402_identity: string
  chain: string
  status: string
  created_at: string
  stats?: {
    volume30d: string
    payments30d: number
    totalFees: string
    revenueShared: string
  }
}

export default function AgentsPage() {
  const [agents, setAgents] = useState<Agent[]>([])
  const [loading, setLoading] = useState(true)
  const [error, setError] = useState<string | null>(null)
  const [showCreate, setShowCreate] = useState(false)

  useEffect(() => {
    loadAgents()
  }, [])

  const loadAgents = async () => {
    try {
      setLoading(true)
      setError(null)
      
      const apiKey = localStorage.getItem('api_key')
      if (!apiKey) {
        setError('API key required')
        return
      }

      const response = await fetch('/api/v1/x402/agents', {
        headers: {
          'Authorization': `Bearer ${apiKey}`,
          'Content-Type': 'application/json',
        },
      })

      if (!response.ok) {
        const data = await response.json()
        throw new Error(data.error || 'Failed to load agents')
      }

      const data = await response.json()
      setAgents(data || [])
    } catch (err) {
      console.error('Load agents error:', err)
      setError(err instanceof Error ? err.message : 'Failed to load agents')
    } finally {
      setLoading(false)
    }
  }

  const formatAmount = (amount: string) => {
    try {
      const bigInt = BigInt(amount)
      const usdc = Number(bigInt) / 1e6
      return usdc.toFixed(2)
    } catch {
      return '0.00'
    }
  }

  return (
    <div>
      <div className="flex items-center justify-between mb-6">
        <div>
          <h1 className="text-3xl font-bold mb-2">x402 Agents</h1>
          <p className="text-gray-400">
            Manage your x402 payment agents and monitor revenue
          </p>
        </div>
        <button
          onClick={() => setShowCreate(true)}
          className="flex items-center gap-2 px-4 py-2 bg-lime-400 text-gray-900 rounded-lg font-medium hover:bg-lime-300 transition-colors"
        >
          <Plus className="w-4 h-4" />
          Create Agent
        </button>
      </div>

      {error && (
        <div className="mb-6 p-4 bg-red-900/20 border border-red-800 rounded-lg flex items-center gap-3">
          <AlertCircle className="w-5 h-5 text-red-400" />
          <span className="text-red-400">{error}</span>
        </div>
      )}

      {loading ? (
        <div className="text-center py-12">
          <div className="inline-block animate-spin rounded-full h-8 w-8 border-b-2 border-lime-400"></div>
          <p className="mt-4 text-gray-400">Loading agents...</p>
        </div>
      ) : agents.length === 0 ? (
        <div className="text-center py-12 bg-gray-900 rounded-lg border border-gray-800">
          <Zap className="w-12 h-12 text-gray-600 mx-auto mb-4" />
          <p className="text-gray-400 mb-4">No agents yet</p>
          <button
            onClick={() => setShowCreate(true)}
            className="px-4 py-2 bg-lime-400 text-gray-900 rounded-lg font-medium hover:bg-lime-300 transition-colors"
          >
            Create Your First Agent
          </button>
        </div>
      ) : (
        <div className="grid gap-4">
          {agents.map((agent) => (
            <AgentCard key={agent.id} agent={agent} onRefresh={loadAgents} />
          ))}
        </div>
      )}

      {showCreate && (
        <CreateAgentModal
          onClose={() => {
            setShowCreate(false)
            loadAgents()
          }}
        />
      )}
    </div>
  )
}

function AgentCard({ agent, onRefresh }: { agent: Agent; onRefresh: () => void }) {
  return (
    <div className="bg-gray-900 rounded-lg border border-gray-800 p-6 hover:border-gray-700 transition-colors">
      <div className="flex items-start justify-between mb-4">
        <div className="flex-1">
          <div className="flex items-center gap-3 mb-2">
            <Zap className="w-5 h-5 text-lime-400" />
            <h3 className="text-xl font-semibold">{agent.name}</h3>
            <span className={`px-2 py-1 text-xs rounded ${
              agent.status === 'active' 
                ? 'bg-green-900/30 text-green-400' 
                : 'bg-gray-800 text-gray-400'
            }`}>
              {agent.status}
            </span>
          </div>
          <div className="grid grid-cols-2 gap-4 text-sm mb-4">
            <div>
              <span className="text-gray-500">x402 Identity:</span>
              <p className="text-gray-300 font-mono text-xs mt-1 break-all">{agent.x402_identity}</p>
            </div>
            <div>
              <span className="text-gray-500">Chain:</span>
              <p className="text-gray-300 mt-1 capitalize">{agent.chain}</p>
            </div>
          </div>
          
          {agent.stats && (
            <div className="grid grid-cols-4 gap-4 pt-4 border-t border-gray-800">
              <div>
                <div className="text-xs text-gray-500 mb-1">30d Volume</div>
                <div className="text-lg font-semibold text-white">
                  ${formatAmount(agent.stats.volume30d || '0')}
                </div>
              </div>
              <div>
                <div className="text-xs text-gray-500 mb-1">30d Payments</div>
                <div className="text-lg font-semibold text-white">
                  {agent.stats.payments30d || 0}
                </div>
              </div>
              <div>
                <div className="text-xs text-gray-500 mb-1">Total Fees</div>
                <div className="text-lg font-semibold text-lime-400">
                  ${formatAmount(agent.stats.totalFees || '0')}
                </div>
              </div>
              <div>
                <div className="text-xs text-gray-500 mb-1">Revenue Shared</div>
                <div className="text-lg font-semibold text-cyan-400">
                  ${formatAmount(agent.stats.revenueShared || '0')}
                </div>
              </div>
            </div>
          )}
        </div>
        <Link
          href={`/dashboard/agents/${agent.id}`}
          className="ml-4 px-4 py-2 bg-gray-800 text-gray-300 rounded-lg hover:bg-gray-700 transition-colors flex items-center gap-2"
        >
          View Details
          <ArrowRight className="w-4 h-4" />
        </Link>
      </div>
    </div>
  )
}

function CreateAgentModal({ onClose }: { onClose: () => void }) {
  const [loading, setLoading] = useState(false)
  const [formData, setFormData] = useState({
    name: '',
    x402_identity: '',
    chain: 'base',
    description: '',
  })

  const handleSubmit = async (e: React.FormEvent) => {
    e.preventDefault()
    setLoading(true)
    
    try {
      const apiKey = localStorage.getItem('api_key')
      if (!apiKey) {
        alert('API key required')
        return
      }

      const response = await fetch('/api/v1/x402/agents', {
        method: 'POST',
        headers: {
          'Authorization': `Bearer ${apiKey}`,
          'Content-Type': 'application/json',
        },
        body: JSON.stringify(formData),
      })

      if (!response.ok) {
        const data = await response.json()
        throw new Error(data.error || 'Failed to create agent')
      }

      onClose()
    } catch (err) {
      alert(err instanceof Error ? err.message : 'Failed to create agent')
    } finally {
      setLoading(false)
    }
  }

  return (
    <div className="fixed inset-0 bg-black/50 flex items-center justify-center z-50">
      <div className="bg-gray-900 rounded-lg border border-gray-800 p-6 max-w-2xl w-full mx-4">
        <h3 className="text-2xl font-bold mb-6">Create x402 Agent</h3>
        <form onSubmit={handleSubmit} className="space-y-4">
          <div>
            <label className="block text-sm font-medium text-gray-300 mb-2">
              Name *
            </label>
            <input
              type="text"
              required
              value={formData.name}
              onChange={(e) => setFormData({ ...formData, name: e.target.value })}
              className="w-full px-4 py-2 bg-gray-800 border border-gray-700 rounded-lg text-white focus:outline-none focus:border-lime-400"
              placeholder="My Payment Agent"
            />
          </div>
          <div>
            <label className="block text-sm font-medium text-gray-300 mb-2">
              x402 Identity *
            </label>
            <input
              type="text"
              required
              value={formData.x402_identity}
              onChange={(e) => setFormData({ ...formData, x402_identity: e.target.value })}
              className="w-full px-4 py-2 bg-gray-800 border border-gray-700 rounded-lg text-white focus:outline-none focus:border-lime-400 font-mono text-sm"
              placeholder="x402://..."
            />
            <p className="text-xs text-gray-500 mt-1">
              Your x402 agent identity string
            </p>
          </div>
          <div>
            <label className="block text-sm font-medium text-gray-300 mb-2">
              Chain *
            </label>
            <select
              required
              value={formData.chain}
              onChange={(e) => setFormData({ ...formData, chain: e.target.value })}
              className="w-full px-4 py-2 bg-gray-800 border border-gray-700 rounded-lg text-white focus:outline-none focus:border-lime-400"
            >
              <option value="base">Base</option>
              <option value="solana">Solana</option>
              <option value="other">Other</option>
            </select>
          </div>
          <div>
            <label className="block text-sm font-medium text-gray-300 mb-2">
              Description
            </label>
            <textarea
              value={formData.description}
              onChange={(e) => setFormData({ ...formData, description: e.target.value })}
              className="w-full px-4 py-2 bg-gray-800 border border-gray-700 rounded-lg text-white focus:outline-none focus:border-lime-400"
              rows={3}
              placeholder="Optional description"
            />
          </div>
          <div className="flex gap-3 pt-4">
            <button
              type="submit"
              disabled={loading}
              className="flex-1 px-4 py-2 bg-lime-400 text-gray-900 rounded-lg font-medium hover:bg-lime-300 transition-colors disabled:opacity-50"
            >
              {loading ? 'Creating...' : 'Create Agent'}
            </button>
            <button
              type="button"
              onClick={onClose}
              className="flex-1 px-4 py-2 bg-gray-800 text-gray-300 rounded-lg hover:bg-gray-700 transition-colors"
            >
              Cancel
            </button>
          </div>
        </form>
      </div>
    </div>
  )
}
